import types from "./types";

export default {
  // this.$store.commit('', '')
  //  user
  [types.GET_USER](state, user) {
    state.user = user;
  },
  // 
  [types.LOGOUT](state) {
    state.user = null;
    localStorage.removeItem("token");
    localStorage.removeItem("token_expire");
  }
};
