import moment from "moment";

const dateFormat = (input, fmtstring) => {
  // momentjs
  //  utc 
  return moment(input)
    .utc()
    .format(fmtstring);
};

const moneyFormat = value => {
  if (!value) return "0.00";
  // /Number(value).toFixed(0)0.511Nils/
  var intPart = Math.floor(value); //
  var intPartFormat = intPart.toString().replace(/(\d)(?=(?:\d{3})+)/g, "1,"); //
  var floatPart = ".00"; //
  var value2Array = value.split(".");
  //=2
  if (value2Array.length == 2) {
    floatPart = value2Array[1].toString(); //
    if (floatPart.length == 1) {
      //0,
      return intPartFormat + "." + floatPart + "0";
    } else {
      return intPartFormat + "." + floatPart;
    }
  } else {
    return intPartFormat + floatPart;
  }
};

const subStrPretty = (value, length) => {
  if (value) {
    if (value.length > length) {
      return value.substr(0, length) + "...";
    } else {
      return value;
    }
  }
  return "";
};

const numPretty = value => {
  if (value) {
    if (value >= 10000) {
      return Math.round((value / 10000) * 100) / 100;
    } else {
      return value;
    }
  }
  return 0;
};

export { dateFormat, moneyFormat, subStrPretty, numPretty };
